* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  #Navbar .hamburger-menu {
    display: none;
  }
  @media screen and (max-width: 500px) {
    #Navbar .hamburger-menu {
      display: flex;
      flex-direction: column;
      padding: 10px 20px;
    }
    #Navbar .hamburger-menu .ham_top {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    #Navbar .hamburger-menu .ham_top img {
      height: 30px;
    }
    #Navbar .hamburger-menu .ham_top button {
      padding: 10px 20px;
      font-size: 1.1rem;
      border: none;
      background-color: transparent;
      border: 1px solid black;
      border-radius: 10px;
      outline: none;
    }
    #Navbar .hamburger-menu .ham_top button:focus {
      background-color: black;
      color: white;
    }
    #Navbar .hamburger-menu .ham_bottom {
      display: none;
      transition: 300ms ease;
    }
    #Navbar .hamburger-menu .ham_bottom.open {
      display: block !important;
      transition: 300ms ease;
    }
    #Navbar .hamburger-menu .ham_bottom.open a {
      display: block;
      text-decoration: none;
      color: black;
      text-align: center;
      font-size: 1.2rem;
      width: 100%;
      padding: 10px 10px;
    }
    #Navbar .nav {
      display: none !important;
    }
  }
  #Navbar .nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    box-shadow: 0 5px 30px rgba(128, 128, 128, 0.605);
  }
  #Navbar .nav div {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #Navbar .nav div a {
    text-decoration: none;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    color: #656565;
    transition: 120ms ease;
  }
  #Navbar .nav div a span {
    padding: 0 10px;
  }
  #Navbar .nav div a span img {
    height: 3rem;
    width: auto;
    margin-right: 40px;
  }
  #Navbar .nav div a:hover {
    color: black;
    transition: 200ms ease;
  }
  #Navbar .nav2 {
    position: absolute;
    transform: translateY(260px);
    z-index: 1;
    background-color: white;
    width: fit-content;
    border-radius: 10px;
    padding: 0px;
    transition: 300ms ease-in;
  }
  @media screen and (max-width: 980px) {
    #Navbar .nav2 a {
      display: block;
      text-align: center;
    }
  }
  #Navbar .nav2 a {
    text-decoration: none;
    color: grey;
    margin: 5px 10px;
    transition: 100ms ease;
  }
  #Navbar .nav2 a:hover {
    color: black;
    transition: 100ms ease;
  }
  #header {
    width: 100%;
    max-height: 600px;
    overflow: hidden;
    position: relative;
  }
  #header img {
    width: 100vw;
    height: auto;
    object-fit: cover;
    filter: brightness(0.8) saturate(1.2);
    transition: 0.5s ease all;
  }
  #header img:hover {
    transform: scale(1.1);
  }
  @media screen and (max-width: 992px) {
    #header img {
      max-height: calc((var(--vh, 1vh) * 100) - 6);
    }
  }
  @media screen and (max-width: 600px) {
    #header {
      height: 80vh;
    }
    #header img {
      width: auto !important;
      height: 100% !important;
    }
    #header .content {
      top: 50% !important;
      left: 50% !important;
      transform: translate(-50%, -50%) !important;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    #header .content a {
      display: block;
    }
  }
  #header .content {
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.674);
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    top: 50%;
    left: 20%;
    transform: translateY(-50%);
  }
  #header .content h1 {
    font-size: 1.8rem;
    text-align: center;
    font-weight: 600;
  }
  #header .content a {
    width: 100%;
    text-align: center;
    text-decoration: none;
    padding: 10px 30px !important;
    color: #e99800;
    font-weight: 600;
  }
  #header .content a:hover {
    color: #9b6500;
  }
  #Home hr {
    margin: 20px 40px;
  }
  #Home .HomeHeading {
    font-weight: 400;
    font-size: 1.6rem;
    text-align: center;
  }
  #Home .townhalls {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 30px;
  }
  #Home .townhalls .townhall {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin: 20px;
    transition: 150ms ease;
  }
  #Home .townhalls .townhall img {
    max-height: 150px;
    height: max-content;
    object-fit: cover;
  }
  #Home .townhalls .townhall:hover {
    scale: 1.2;
    transition: 150ms ease;
  }
  #Home .info {
    display: flex;
    justify-content: center;
  }
  #Home .info img {
    width: 40vw;
  }
  @media screen and (max-width: 600px) {
    #Home .info img {
      width: 80vw !important;
    }
  }
  #Home .info .data {
    width: 40vw;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #Home .info .data h1 {
    font-size: 2rem;
    padding: 10px;
    margin: 20px;
    text-align: center;
  }
  #Home .info .data p {
    font-size: 1.3rem;
    margin: 10px;
    text-align: center;
    padding: 10px;
  }
  @media screen and (max-width: 700px) {
    #Home .info {
      display: block;
      max-height: calc((var(--vh, 1vh) * 100) - 6);
    }
    #Home .info img {
      width: auto;
    }
    #Home .info .data {
      width: auto;
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  #Home .blogs {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #Home .blogs div {
    width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  #Home .blogs div img {
    height: 300px;
    width: auto;
    border-radius: 10px;
    object-fit: cover;
  }
  @media screen and (max-width: 700px) {
    #Home .blogs div {
      width: 100%;
    }
    #Home .blogs div img {
      height: auto;
      width: 90vw;
      border-radius: 10px;
      object-fit: cover;
    }
  }
  #Home .blogs div h1 {
    font-size: 1.7rem;
    font-weight: 500;
    text-align: center;
    margin: 30px;
  }
  #Home .blogs div button {
    padding: 10px 40px;
    background-color: transparent;
    border: none;
    border: 2px solid orange;
    border-radius: 10px;
    font-size: 1.3rem;
    transition: 100ms ease;
  }
  #Home .blogs div button:hover {
    transition: 100ms ease;
    background-color: orange;
  }
  @media screen and (max-width: 700px) {
    #Home .blogs {
      display: block;
      max-height: calc((var(--vh, 1vh) * 100) - 6);
    }
    #Home .blogs img {
      width: auto;
    }
    #Home .blogs .data {
      width: auto;
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  #Home .support {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  #Home .support div {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 20px;
    margin: 20px;
  }
  #Home .support div img {
    height: 200px;
  }
  #Home .support div h1 {
    font-size: 1.8rem;
    font-weight: 500;
    text-align: center;
    margin: 20px;
  }
  #Home .support div h3 {
    margin: 20px;
    font-size: 1.3rem;
    font-weight: 300;
    text-align: center;
  }
  #Footer .top {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 20px 20px;
    border-top: 2px solid black;
  }
  @media screen and (max-width: 600px) {
    #Footer .top {
      flex-direction: column;
    }
  }
  #Footer .top .logo img {
    height: 200px;
  }
  #Footer hr {
    margin: 30px 0px;
  }
  #Footer div h1, #Footer div h2 {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    margin: 20px 0px;
  }
  #Footer div .socials {
    text-align: center;
  }
  #Footer div .socials svg {
    height: 30px;
    transition: 100ms ease;
  }
  #Footer div .socials svg:hover {
    scale: 1.2;
    transition: 100ms ease;
  }
  #Footer div ul {
    list-style-type: none;
  }
  #Footer div ul li {
    text-align: center;
    margin: 10px 0px;
    color: grey;
  }
  #Footer div ul li:hover {
    color: black;
  }
  #Footer .bottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px;
    margin-bottom: 40px;
  }
  #Footer .bottom a {
    text-decoration: none;
    color: orange;
    font-size: 1.2rem;
    margin: 20px 40px;
  }
  #Footer .bottom a:hover {
    color: #9b6603;
  }
  #login {
    min-height: 90vh;
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #dbdbdb;
    align-items: center;
  }
  #login .formWrapper {
    margin: 50px 20px;
    background-color: #ededed;
    border: 2px solid #fbbc45;
    box-shadow: 2px 2px 10px rgba(255, 166, 0, 0.447);
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 400px;
  }
  #login .formWrapper .logo {
    text-align: center;
    font-size: 2.1rem;
    color: #1f1f1f;
    margin: 10px;
  }
  #login .formWrapper .title {
    text-align: center;
    font-size: 1.6rem;
    color: gray;
    margin: 5px;
  }
  #login .formWrapper form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #login .formWrapper form input {
    margin: 5px;
    background-color: transparent;
    width: 100%;
    padding: 10px;
    font-size: 1.2rem;
    border: none;
    border-bottom: 2px solid #222;
  }
  #login .formWrapper form button {
    padding: 5px 40px;
    margin: 10px;
    font-size: 1.2rem;
    background-color: orange;
    border: none;
    border-radius: 10px;
  }
  #login .formWrapper .google {
    padding: 10px 40px;
    border-radius: 20px;
    background-color: #e85100;
    color: white;
    font-size: 1.3rem;
    font-weight: 500;
    border: none;
    margin: 10px;
  }
  #Saletiles {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    padding: 10px;
    padding-left: 10vw;
    padding-right: 10vw;
    margin: 10px;
  }
  .SaleTile {
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    transition: 300ms ease-in-out;
    box-shadow: 1px 1px 5px rgba(128, 128, 128, 0.874);
  }
  .SaleTile img {
    width: 200px;
    height: auto;
    transition: opacity 0.3s ease-in-out;
  }
  .SaleTile h1 {
    font-size: 1.4rem;
    color: grey;
  }
  .SaleTile button {
    padding: 10px;
    margin: 10px;
    outline: none;
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
    background-color: transparent;
    border: 3px solid orange;
    transition: 300ms ease-in-out;
  }
  .SaleTile button:hover {
    background: orange;
    color: white;
    transition: 300ms ease-in-out;
  }
  .SaleTile:hover {
    box-shadow: 5px 5px 10px rgba(128, 128, 128, 0.975);
    transition: 300ms ease-in-out;
  }
  #Big_view {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(217, 217, 217, 0.537);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #Big_view .content {
    max-height: 750px;
    height: auto;
    border-radius: 30px;
    padding: 20px;
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: black;
    background-color: white;
  }
  #Big_view .content .img {
    flex: 1;
  }
  #Big_view .content .img img {
    max-height: 400px;
    height: 30vh;
    width: auto;
    margin: 10px 40px;
    border-radius: 10px;
    border: 1px solid black;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .img img {
      height: 170px !important;
      margin: 5px 50px;
    }
  }
  #Big_view .content .data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #Big_view .content .data h1 {
    font-size: 2.4rem;
    font-weight: 400;
    color: black;
    text-align: center;
    display: inline;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .data h1 {
      font-size: 1.8rem;
    }
  }
  #Big_view .content .data h3 {
    font-size: 1.8rem;
    font-weight: 400;
    text-align: center;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .data h3 {
      font-size: 1.6rem;
    }
  }
  #Big_view .content .data p {
    font-size: 1.5rem;
    display: inline;
    margin: 20px;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .data p {
      font-size: 1.2rem;
    }
  }
  #Big_view .content .data div {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    align-items: center;
    padding: 20px;
    margin: 10px;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .data div {
      padding: 0px;
      margin: 5px;
    }
  }
  #Big_view .content .data div button {
    display: block;
    padding: 10px;
    margin: 10px;
    outline: none;
    border: none;
    border-radius: 5px;
    font-size: 1.5rem;
    background-color: transparent;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .data div button {
      padding: 5px;
      margin: 5px;
      font-size: 1rem;
    }
  }
  #Big_view .content .data div button.cart {
    border: 3px solid greenyellow;
    transition: 100ms ease;
  }
  #Big_view .content .data div button.cart:hover {
    background-color: greenyellow;
    transition: 100ms ease;
  }
  #Big_view .content .data div button.buy {
    border: 3px solid orange;
    transition: 100ms ease;
  }
  #Big_view .content .data div button.buy:hover {
    background-color: orange;
    transition: 100ms ease;
  }
  #Big_view .content .data div button.close {
    border: 3px solid red;
    transition: 100ms ease;
  }
  #Big_view .content .data div button.close:hover {
    background-color: red;
    transition: 100ms ease;
    color: white;
  }
  @media screen and (max-width: 600px) {
    #Big_view .content .data div {
      display: flex;
      flex-direction: column;
    }
    #Big_view .content .data div .img {
      flex: 1;
    }
    #Big_view .content .data div .img img {
      max-height: 200px;
      width: auto;
      margin: 10px 10px;
    }
    #Big_view .content .data div .data {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    #Big_view .content .data div .data h1 {
      font-size: 1.6rem;
    }
    #Big_view .content .data div .data h3 {
      font-size: 1.4rem;
      font-weight: 400;
      text-align: center;
    }
    #Big_view .content .data div .data p {
      font-size: 1.5rem;
      display: inline;
      margin: 20px;
    }
  }
