  #user {
    min-height: 700px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #user .profile {
    margin: 30px;
  }
  #user .profile h1 {
    margin: 10px;
    font-weight: 400;
  }
  #user .profile h1 strong {
    font-weight: 400;
    text-decoration: underline;
  }
  #user button {
    outline: none;
    width: 200px;
    border: none;
    border: 1px solid grey;
    border-radius: 5px;
