    font-size: 1.4rem;
    margin: 10px;
  }
  .Loading_pg {
    padding: 30px;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 20px;
    justify-content: center;
    align-items: center;
  }
  .Loading_pg h1 {
    font-size: 2.5rem;
    font-weight: 400;
    text-align: center;
    width: 100%;
  }
  .Loading_pg h1.loading {
    animation: loading 2s infinite ease forwards;
  }
  .Loading_pg h1.Error {
    color: red;
    font-size: 2.5rem;
    font-weight: 400;
  }
  .Loading_pg button.Error {
    padding: 10px 20px;
    margin: 20px;
    border: none;
    border-radius: 5px;
    background-color: #8e0000;
    color: white;
  }
  @keyframes loading {
    0% {
      content: "LOADING.";
      transform: translateX(-6%);
      opacity: 0.9;
    }
    25% {
      content: ".";
      transform: translateX(0);
      opacity: 0.9;
    }
    50% {
      content: "..";
      transform: translateX(6%);
      opacity: 0.9;
    }
    75% {
      content: "...";
      transform: translateX(0);
      opacity: 0.9;
    }
    100% {
      content: "";
      transform: translateX(0);
      opacity: 1;
      visibility: hidden;
      display: none;
    }
  }
  